.banner {
  width: 100%;
}
.banner_link {
  position: relative;
  display: inline-block;
  color: #000;
  width: 100%;
  vertical-align: top;
}
.banner_img_area ~ .banner_text_area {
  border-top: 1px solid #ccc;
}
.banner_text_area {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  box-sizing: border-box;
}
.banner_title {
  font-size: 12px;
  line-height: 20px;
  font-weight: bold;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-height: 20px;
  max-height: 40px;
}
.banner_close_button {
  position: relative;
  width: 26px;
  height: 26px;
  color: transparent;
  overflow: hidden;
  background-color: #000;
}
.banner_close_button::before, .banner_close_button::after {
  z-index: 10;
  height: 15px;
  width: 1px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  content: "";
}
.banner_close_button::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.banner_close_button::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.banner_close_area {
  position: absolute;
  left: 0;
  top: 0;
}
.banner_wrap {
  width: 100%;
  max-width: 35%;
  position: fixed;
  bottom: 30%;
  right: -100%;
  transition: right 0.4s;
  z-index: 1050;
  display: none;
}
@media screen and (min-width: 992px) {
  .banner_wrap {
    max-width: 15%;
    bottom: 15%;
  }
}
.banner_wrap.fixed {
  right: 0;
}
.banner_wrap.active {
  display: block;
}